<template lang="html">
  <div class="">
    <jwdheaders></jwdheaders>
    <el-row style="width:1200px;margin:0 auto;">
      <el-col :span="4"><div class="grid-content bg-purple">
        <jwdzly></jwdzly>
      </div></el-col>
      <el-col :span="20"><div class="grid-content bg-purple">
        <!--内容-->
            <div class="container">
              <div class="txhead">
               <span>提现</span>
              </div>
            <!--步骤条-->
                <el-steps :space="200" :active="1" center=true>
                <el-step title="填写银行卡/金额"></el-step>
                <el-step title="确认提现信息"></el-step>
                <el-step title="申请提现成功"></el-step>
                </el-steps><!--步骤条结束-->
                <!--提示信息-->
             <p class="tishi">
               <i class="el-icon-information"></i>
                可提现金额25.64元，充值金额0.00元。充值金额不许提现，如需返回，请点击“
                <span>发起充值退回</span>”
             </p>
                <!--添加银行卡-->

                <el-upload
                    class="upload-demo"
                    action="https://jsonplaceholder.typicode.com/posts/"
                    :on-change="handleChange"
                    :file-list="fileList3">
                    <span>选择银行卡:</span>

                    <el-input class="bank" placeholder="招商银行"></el-input>

                    <el-button><i class="el-icon-plus"></i>添加银行卡（最多添加9张）</el-button>

                </el-upload><!--添加银行卡结束-->
                <!--输入金额-->
                <div class="srje">
                <span>提现金额:</span>
                <el-input placeholder="请输入金额"></el-input>&nbsp;
                元<br/>
                <p class="money-info">
                 提现金额每次最高2万元，每日限3次，每日累计最高2万元，每月累计最高6万元，<br/>
                 安装数字证书后，交易更加安全可提升限额，建议您 <a>立即安装数字证书</a>
                </p>
                </div>

                <p class="txtime">到账时间：14:00前提现次日到账，14：00点后填写后天到账<span>(免费)</span></p>
                <!--输入金额结束-->

                 <router-link to="/jwdtixian4" class="el-button next" tag="button">
                 下一步</router-link>

            </div> <!--内容结束-->
      </div></el-col>
    </el-row>

  </div>
</template>

<script>
import jwdzly from "@/components/jwdzly"
import jwdheaders from "@/components/jwdheads"
export default {
  components:{
    jwdheaders,
    jwdzly
  },


}
</script>

<style lang="less" scoped>
.header-wrapper{
    width: 1200px;

    margin: 0 auto;
    border-bottom:1px solid grey;
    &:after{
      content: '';
      display: block;
      clear: both;
    }
    img{
      float: left;
      padding: 7px 0;
    }
    ul{
      float: left;
      &:after{
        content: '';
        display: block;
        clear: both;
      }
      li{
        float: left;
        font-size: 18px;
        color: #343434;
        margin: 0 25px;
        line-height: 80px;
        &.act{
          color: #2e51a5;
          font-weight: bolder;
          border-bottom: 2px solid #2e51a5;
        }
      }
    }

  }
   .container{
      font-text:12px;
      width:790px;
      margin:20px;
      padding:20px;
      border:1px solid black;
     .txhead{
         border-bottom:1px solid #d0d0d0;
         padding-bottom:10px;
         span{
             font-text:16px;
             font-weight:bolder;

         }
     }

     .el-steps{
         margin:50px auto;
     }
     .tishi{
         width:auto;
         background:#ffff8d;
         color:#ff8814;
         padding:10px;
         margin-left:188px;
         margin-top:45px;
         span{
             color:#6abfa0;
             cursor:pointer;
         }
     }
     .el-upload{
         span{
             color:grey;
             width:100px;
             display:inline-block;
             text-align:right;
         }
         .el-button{
             border:2px dashed #e5e2e2;
             color:#e5e2e2;
             margin:22px 0;
             padding:10px;
         }
         .bank{
         width:210px;
     }
     }
     .srje{
         span{
            color:grey;
            width:100px;
             display:inline-block;
             text-align:right;
         }
         color:#e5e2e2;
         .el-input{
             width:210px;
             display:inline-block;
         }
         .money-info{
             margin-left:200px;
             margin-top:24px;
             color:#babbba;
             a{
                 color:#5493cd;
                 cursor:pointer;
             }
         }
     }
     .txtime{
         color:#60757e;
         margin:24px 0 24px 200px;
         span{
            color :#b8bbc0;
         }
     }
     .next{
         margin:24px 0 24px 200px;

             background:red;
             color:white;
     }


 }
</style>



